				"id": "/home/austin/FuSC/src/pages/2017-05-31-I-still-write-about-comics/index.md absPath of file >>> MarkdownRemark",
				"html": "<p><em>Deja vu</em> eh?</p>\n<p>When I started <em>Retcon Rasengan</em> I did it because I needed a place where I wouldn’t feel pressure to get things out on deadlines.  I also wanted to be able to do whatever I wanted format-wise and not feel bad about egregious picture placement or egregious word-vomit of the type I’m already indulging in now.</p>\n<div style=\"\">\n  <figure class=\"floatCenter\">\n    <a target='_blank' href=\"http://retconrasengan.blogspot.com/2016/04/hand-sewn-domestic-female-aesthetic.html\">\n      <img class=\"linkImage\" style=\"max-height: 380px;padding: 30px; background-color: rgba(92,107,192, 0.1);border-radius: 3px;\" src=\"https://2.bp.blogspot.com/-0ipM8FHFpYo/VxaLuN9yRqI/AAAAAAAAFuM/mncK6PMa-Ccscl8Z1mgBgBltXv5Eu6TWQCKgB/w1200-h630-p-k-no-nu/kendrafemaleaestetic.png\">\n    </a>\n    <figcaption style=\"color: black;margin-top: -30px;\">RR was shortlived; my interview with Kendra Josie Kirkpatrick was a highlight</figcaption>\n  </figure>\n</div>\n<p>But I was going through some shit.</p>\n<p>By the time early 2016 rolled around, I had been out of my Master’s program for a full year and had no prospects for a job.  I started working part-time for a non-profit, which was certainly better than 1. nothing and 2. washing dishes; but… well, you get it, I hope.</p>\n<p>And I was so hungry to write. I started with the lovely folks at <a target='_blank' href='http://comicbastards.com'>Comic Bastards</a> when Dustin gave me a chance to shit up his sight fawning over <em>Naruto</em> or spewing typographic venom at the very concept of motion comics.  I reached out to <a target='_blank' href='http://loser-city.com'>Loser City</a> to write even more, and have the unique pleasure of both resurrecting and re-murdering their newsletter.  I wrote a couple of pieces for Comics Beat after being approached by somebody who just straight up stopped responding to my DMs, which I mention not to be passive-aggressive but only because it’s all pure humor to me now.  And, of course, the incomparable Danny Elk-elks started pestering me around the time RR was burning up in the atmosphere, pelting me with the absolute weirdest shit I had ever seen.  I still write for him over at <a target='_blank' href='http://comicsbulletin.com'>Comics Bulletin</a> and will continue to do so.  </p>\n<div style=\"\">\n  <figure class=\"floatCenter\">\n    <a target='_blank' href=\"http://comicsbulletin.com/tiny-pages-made-ashes-51917-mirror-mirror-ii/\">\n    <img style=\"padding: 30px; background-color: rgba(92,107,192, 0.1); max-height: 480px;border-radius: 3px;\" src=\"https://i2.wp.com/comicsbulletin.com/wp-content/uploads/2017/05/Mirror-Mirror-II-cover.jpg?fit=798%2C1200\" alt=\"Mirror Mirror II Review\">\n    </a>\n    <figcaption style=\"color:black;margin-top:-30px;\">Peep my in-depth review of Mirror Mirror II</figcaption>\n  </figure>\n</div>\n<p>I also once e-mailed Andrew Wheeler to write for Comics Alliance.  He never got back to me.  </p>\n<p>A better summation of my clout I cannot figure.</p>\n<p>I really enjoyed doing it.  Nothing felt better to me than when I would crush a book in a day or two and then proceed to wade through the canals that had formed in the space between my feelings and the most prominent artistic outcroppings of any given work.  If I write anything, it’s because I felt like I could at least convey that to a <em>single</em> person other than myself in a way that felt efficacious and sincere and not forced or pithy.</p>\n<p>But the problem lies there as well.  Maybe it’s not just comics twitter, but at certain points. the cynicism is infectious, and your ability to calibrate your motivations based on your own compass becomes seriously compromised when you let your empathy feed your self-pity rather than mediating the two. It’s just so easy to internalize all the negativity in comic-dom, both the inevitable stuff and the nasty nastiness.</p>\n<p>Criticism, or even straight-up exploring a work in terms of what parts of it worked for you, is a way to share excitement and to share frustration; it is, somehow, simultaneously cathartic and taxing in ways that are hard to balance. If you leave that fucking sharingan on you can glean a lot, but you’re going to fuck up your day; you were just trying to read a funnybook, you utter clown.</p>\n<p>I hope this is not trite, but it is a privilege to be able and willing to make the commitment to write regularly in general, let alone analyze, let alone share one’s thoughts about works that others have created, or already engaged with in ways that often seem more interesting.  Life balance plays a huge part in that privilege, and it was one that I lacked as RR tapered off and one that I am lucky to have now. None of this is to even mention the fact that I have the unfortunately ultimate privilege of being a cis-het straight white guy who does not have to worry about the way in which a given space might respond to my particular voice.</p>\n<p>Nobody is wrong for walking away. Often it is very obviously not a <em>lack</em> of conviction that takes some of our most talented brothers and sisters out of the spotlight. Nobody has to do this. I mean, nobody has to do anything as far as hobbies go, right? And certainly nobody has to do <em>this</em>, this fucking.. what? Hobby <em>about</em> a hobby?</p>\n<p>But for me, I know I wasn’t giving it my all.  And in the last year, I learned that I actually have one of those: an “all”.  I also found my “give a shit” as well as my “some semblance of time management,” as if to feign adulthood.</p>\n<p>More importantly, I love that navigation I mentioned earlier.  That tug-of-war between what seems to be the work-in-itself and my own reaction to it. When a book really clicks and you feel like you are on the same wavelength not as the author but the wavelength of the <em>book</em>, the <em>panel</em>, that <em>layout</em>— when you can speak truth to the beauty in the rickety joints of that good ol’ visual narrative syntax, it feels like walking somewhere you shouldn’t be.  </p>\n<p>The world seems bigger because you get to open up a space between yourself and a piece of art. There’s a beautiful vulnerability there, opening your viewpoint to be part of this shared experience. </p>\n<p>It’s late, and I’ve spent enough time on me.</p>\n<img style=\"max-height: 480px;\" src=\"http://fuckupsomecomics.com/posts/1/future_hendrix.png\" alt=\"And so\" />\n<p>Let’s fuck up some comics.</p>",
